import GithubIcon32 from '@carbon/icons-react/lib/logo--github/32';
import SettingsIcon32 from '@carbon/icons-react/lib/settings/32';
import { Loading, ModalWrapper, TextInput } from 'carbon-components-react';
import {
  Header,
  HeaderGlobalAction,
  HeaderGlobalBar,
  HeaderName
} from 'carbon-components-react/lib/components/UIShell';
import React, { useState } from 'react';
import { GENERIC_ERROR } from '../constants';

const AppHeader = ({ setNotification }) => {
  const [apikey, setApikey] = useState('');
  const [url, setUrl] = useState('');
  const [assistantId, setAssistantId] = useState('');
  const [skill, setSkill] = useState('');
  const [pw, setPw] = useState('');
  const [loading, setLoading] = useState(false);

  const changeCredentialsHandler = async () => {
    setLoading(true);
    const rawResponse = await fetch('/api/credentials', {
      method: 'POST',
      body: JSON.stringify({
        apikey,
        url,
        assistantId,
        skill
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    setLoading(false);
    if (rawResponse.ok !== true) {
      setNotification({
        type: 'error',
        title: 'Error',
        msg: GENERIC_ERROR
      });
    } else {
      const response = await rawResponse.json();
      if (response.err === true) {
        setNotification({
          type: 'error',
          title: 'Error',
          msg: response.msg
        });
      } else {
        setNotification({
          type: 'success',
          title: 'Éxito',
          msg: response.msg
        });
      }
    }
  };

  let spinner = null;
  if (loading === true) spinner = <Loading description='Cargando...' />;

  return (
    <Header aria-label={`Maratona Behind the Code 2020 Desafío 1`}>
      <HeaderName href='/' prefix='Maratona Behind the Code 2020'>
        Desafío 1
      </HeaderName>
      <HeaderGlobalBar>
        <HeaderGlobalAction
          aria-label='GitHub'
          onClick={() =>
            window.open('https://github.com/maratonadev-la/desafio-1-2020')
          }
        >
          <GithubIcon32 />
        </HeaderGlobalAction>
        <ModalWrapper
          modalLabel='Modificar credenciales'
          modalHeading={
            'Para usar un asistente diferente, ingresa tus credenciales de Watson Assistant y tu contraseña'
          }
          primaryButtonText='Enviar'
          secondaryButtonText='Cancelar'
          iconDescription='Modificar credenciales'
          renderTriggerButtonIcon={() => <SettingsIcon32 />}
          buttonTriggerClassName='icon-button'
          style={{ textAlign: 'left' }}
          handleSubmit={changeCredentialsHandler}
        >
          <TextInput
            id='apikey'
            placeholder='Inserta tu API key aquí...'
            labelText='Clave API del servicio Watson Assistant (API KEY)'
            value={apikey}
            onChange={(e) => setApikey(e.target.value)}
          />
          <br />
          <TextInput
            id='url'
            placeholder='Inserta tu URL aquí...'
            labelText='URL para la llamada a la API del servicio Watson Assistant'
            value={url}
            onChange={(e) => setUrl(e.target.value)}
          />
          <br />
          <TextInput
            id='assistantId'
            placeholder='Inserta tu Assistant ID aquí...'
            labelText='ID del Watson Assistant (Assistant ID)'
            value={assistantId}
            onChange={(e) => setAssistantId(e.target.value)}
          />
          <br />
          <TextInput
            id='skill'
            placeholder='Inserta tu Skill ID aquí...'
            labelText='ID de la skill del Watson Assistant (Skill ID)'
            value={skill}
            onChange={(e) => setSkill(e.target.value)}
          />
          <br />
          <TextInput
            id='password'
            type='password'
            placeholder='Inserta tu contraseña aquí...'
            labelText='Contraseña elegida durante el despliegue'
            value={pw}
            onChange={(e) => setPw(e.target.value)}
          />
          {spinner}
        </ModalWrapper>
      </HeaderGlobalBar>
    </Header>
  );
};

export default AppHeader;
