import { Renew32, SendFilled32 } from '@carbon/icons-react';
import { CodeSnippet, Loading, TextInput } from 'carbon-components-react';
import React, { useEffect, useState } from 'react';
import { GENERIC_ERROR } from '../constants';

const Chat = ({ setNotification }) => {
  const [value, setValue] = useState('');
  const [responseJson, setResponseJson] = useState({});
  const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(false);

  const sendToWatson = async (msg, prevHistory) => {
    const rawResponse = await fetch('/api/message', {
      method: 'POST',
      body: JSON.stringify({
        msg
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    if (rawResponse.ok !== true) {
      setNotification({
        type: 'error',
        title: 'Error',
        msg: GENERIC_ERROR
      });
    } else {
      const response = await rawResponse.json();
      if (response.err === true) {
        setNotification({
          type: 'error',
          title: 'Error',
          msg:
            'La sesión ha expirado. Haga clic en el botón de arriba para crear una nueva sesión.'
        });
      } else {
        setResponseJson(response.result);
        const newHistory = [...prevHistory];
        response.result.output.generic.forEach((msg) => {
          if (msg.response_type !== 'text') {
            setNotification({
              type: 'warning',
              title: 'Mensaje de tipo desconocido recibido',
              msg: (
                <>
                  Mensajes de tipo{' '}
                  <span style={{ fontStyle: 'italic' }}>
                    {msg.response_type}
                  </span>{' '}
                  no son compatibles
                </>
              )
            });
          } else {
            newHistory.push({
              from: 'watson',
              msg: msg.text
            });
          }
        });
        setHistory(newHistory);
      }
    }
  };

  const renewSession = async () => {
    setHistory([]);
    setResponseJson({});
    const rawResponse = await fetch('/api/session', {
      method: 'POST'
    });
    if (rawResponse.ok !== true) {
      setNotification({
        type: 'error',
        title: 'Error',
        msg: GENERIC_ERROR
      });
    } else {
      const response = await rawResponse.json();
      if (response.err === true) {
        setNotification({
          type: 'error',
          title: 'Error',
          msg:
            'No se pudo iniciar una sesión. Asegúrese de que sus credenciales sean correctas'
        });
      } else {
        sendToWatson('', []);
      }
    }
  };

  const sendMessageHandler = async (msg) => {
    if (msg) {
      const newHistory = [...history];
      newHistory.push({ from: 'user', msg });
      setHistory(newHistory);
      setValue('');
      sendToWatson(msg, newHistory);
    }
  };

  useEffect(() => {
    setLoading(true);
    renewSession().finally(() => setLoading(false));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const chat = document.getElementById('chat-container');
    chat.scrollTop = chat.scrollHeight;
  }, [history, value]);

  let spinner = null;
  if (loading === true) {
    spinner = <Loading />;
  }

  return (
    <>
      <div
        className='bx--row'
        id='chat-container'
        style={{
          height: '45%',
          overflow: 'auto'
        }}
      >
        <div className='bx--col' style={{ overflow: 'auto' }}>
          <div className='chat'>
            <button
              onClick={() => {
                setLoading(true);
                renewSession().finally(() => setLoading(false));
              }}
              className='renew-session'
            >
              <Renew32 />
            </button>
            {spinner}
            {history.map((m, i) => {
              return (
                <p key={m.msg + i + Date.now()} className={m.from}>
                  {m.msg}
                </p>
              );
            })}
          </div>
        </div>
      </div>
      <div style={{ position: 'relative' }}>
        <TextInput
          id='message'
          labelText={null}
          placeholder='Escribe un mensaje...'
          onKeyPress={(e) => {
            if (e.key === 'Enter') sendMessageHandler(value);
          }}
          value={value}
          onChange={(e) => setValue(e.target.value)}
        />
        <button
          className='send-message'
          style={{
            visibility: value === '' ? 'hidden' : 'visible'
          }}
          onClick={() => sendMessageHandler(value)}
        >
          <SendFilled32 />
        </button>
      </div>
      <div
        className='bx--row'
        style={{ marginTop: '1rem', marginBottom: '1rem' }}
      >
        <div className='bx--col'>
          <h3>JSON de la última respuesta:</h3>
          <CodeSnippet
            type='multi'
            showMoreText='Mostrar más'
            showLessText='Mostrar menos'
            copyButtonDescription='Copiar al portapapeles'
            copyLabel='Copiar'
            feedback='Copiado!'
            onClick={() =>
              navigator.clipboard.writeText(
                JSON.stringify(responseJson, null, 2)
              )
            }
          >
            {JSON.stringify(responseJson, null, 2)}
          </CodeSnippet>
        </div>
      </div>
    </>
  );
};

export default Chat;
