import { Loading, ModalWrapper, TextInput } from 'carbon-components-react';
import React, { useState } from 'react';
import { GENERIC_ERROR } from '../constants';

const SidePanel = ({ setNotification }) => {
  const [pw, setPw] = useState('');
  const [loading, setLoading] = useState(false);

  const submitHandler = async () => {
    setLoading(true);
    const rawResponse = await fetch('/api/submit', {
      method: 'POST',
      body: JSON.stringify({
        password: pw
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    });
    if (rawResponse.ok !== true) {
      setNotification({
        type: 'error',
        title: 'Error',
        msg: GENERIC_ERROR
      });
    } else {
      const response = await rawResponse.json();
      if (response.err === true) {
        setNotification({
          type: 'error',
          title: 'Error',
          msg: response.msg
        });
      } else {
        setNotification({
          type: 'success',
          title: '¡Ya casi terminamos!',
          msg: response.msg
        });
      }
    }
    setLoading(false);
  };

  let spinner = null;
  if (loading === true) {
    spinner = <Loading description='Enviando...' />;
  }

  return (
    <div
      className='bx--header-panel bx--header-panel--expanded side-panel'
      style={{ zIndex: 2 }}
    >
      <h2>Desafío 1 | Alestra</h2>
      <br />
      <p style={{ textAlign: 'center' }}>
        Desarrolla un asistente virtual que identifica y responde adecuadamente
        a los intents de Saludo, Adios y Request. Para el intent de Request el
        asistente debe responder informando el tipo de solicitud y aplicación,
        junto con un número de ticket generado para esta solicitud.
      </p>
      <br />
      <ModalWrapper
        shouldSubmitOnEnter
        buttonTriggerText='Enviar'
        modalHeading='Atención: Después de hacer clic en enviar, recibirás un correo electrónico pidiéndote que confirmes tu envío haciendo clic en un enlace. Debes hacer clic en el enlace para completar el envío.'
        modalLabel='Envío'
        primaryButtonText='Enviar'
        secondaryButtonText='Cancelar'
        handleSubmit={submitHandler}
        style={{ zIndex: 99999 }}
      >
        <TextInput
          id='password'
          type='password'
          value={pw}
          onChange={(e) => setPw(e.target.value)}
          labelText='Ingresa la contraseña elegida durante el despliegue para enviar'
          placeholder='Inserta tu contraseña aquí...'
        />
        {spinner}
      </ModalWrapper>
    </div>
  );
};

export default SidePanel;
